import React, { Component } from 'react'

export default class hexagon extends Component {

    handleClick = () => {
        const {changeDatas, backgroundImages, title, level, experience, where} = this.props
        changeDatas(backgroundImages, title, level, experience, where)
        
        this.props.Popup();
    }

    render() {
        const { backgroundImages } = this.props

        return (
            <li className="hex" onClick={this.handleClick}>
                <div className="hexIn ">
                    <a className="hexLink">
                    <img src={ backgroundImages } alt="" />
                    </a>
                </div>
            </li>
        )
    }
}
