import React, { Component } from 'react'
import ProjectFolder from './projectFolders'

import TatuajesCtm from '../images/tatuajesctm.jpg'
import TatuajesCtmPage from '../images/tatuajesctmPage.png'

import '../styles/projects.css'

const styles = {
    h1: {
        textAlign: "center",
        color: "#fff",
        fontFamily: "'Helvetica Neue', 'Lucida Grande', Helvetica, Arial, Verdana, sans-serif",
        color: "#d3d3d3",
        fontSize: "2.5em"
    },
    center: {
        position: "absolute",
        left:" 50%",
        top: "50%",
        transform: "translate(-50%, -50%)",
        textAlign: "center",
    },
    headLine:{
        fontFamily:"Georgia,serif",
        color:"#7e7e7e",
        fontVariant: "small-caps", 
        textTransform: "none",
        fontWeight: "100",
        marginBottom: "0",
        fontSize: "5em",
    },
    projects: {
        width: "80%", 
        margin: "0 auto"
    }
}

export default class Projects extends Component {
    
    state = {
        seen: false, 
        page: null,
        title: "",
        allDescription: "",
        url: "",
        cliente: "",
        tecnology: "",
    }

    togglePop = () => {
        this.setState({
         seen: !this.state.seen
        })
    }

    projectFolders = (page, title, allDescription, url, cliente, tecnology) =>{
        this.setState({page: page})
        this.setState({title: title})
        this.setState({allDescription: allDescription})
        this.setState({url: url})
        this.setState({cliente: cliente})
        this.setState({tecnology: tecnology})
    }

    render() {
        
        const project = [
            [TatuajesCtmPage, TatuajesCtmPage, "TatuajesCTM", "Page for a tattoo artist", "This page has index, contact email, image gallery, login, calendar, CRUD data", "http://tatuajesctm.com/", "Mario Henriquez Bravo", "HTML5, CSS(Bootstrap-Manual), PHP(codeigniter), PhpMyAdmin"],
        ]

        var projects =[];
        for(var i=0;i<project.length;i++){
             // push the component to programmers!
             projects.push(<ProjectFolder 
                img = { project[i][0] } 
                page = { project[i][1] }
                title = { project[i][2] } 
                description = { project[i][3] }
                allDescription = { project[i][4] }
                url = { project[i][5] }
                cliente = { project[i][6] }
                tecnology = { project[i][7] }
                changeDatas = {this.projectFolders}
                Popup = {this.togglePop}
            />);
        }

        return (
            <div style={{paddingTop: "20px"}}>
                
                <h1 style={styles.h1}>Projects</h1>
                <div className="container">
                    <div style={styles.projects}>{projects}</div>
                </div>
                {this.state.seen ? 
                    <div id="preview" className="modall">
                        <div className="modall-content">
                            <span className="close" onClick={this.togglePop}>&times;</span>
                            <img id="img" className="img-fluid" src={this.state.page}/>
                            <div id="details">
                                <h3 id="title">{this.state.title} </h3>
                                <p id="info"> <strong>Description:</strong> {this.state.allDescription}  </p>
                                <p id="info"><strong>Tecnologies:</strong> {this.state.tecnology}  </p>
                                <p id="info"><strong>Cliente:</strong> {this.state.cliente}  </p>
                                <div className="button" id="live" ><a href={this.state.url} style={{textDecoration:"none", color: "#fff"}}>View Page</a></div>
                                <i className="fab fa-github-square" id="github"></i>
                            </div>
                        </div>
                    </div> 
                    : null}
            </div>
        )
    }
}
