import React, { Component } from 'react'

export default class projectFolders extends Component {
   
    handleClick = () => {

        const {changeDatas, page, title, allDescription, url, cliente, tecnology} = this.props
        changeDatas(page, title, allDescription, url, cliente, tecnology)
        
        this.props.Popup();
    }

    render() {
        const { title, description, img } = this.props
        return (
            <div style={{marginLeft: "5px", marginRight: "5px"}}>
                <div className="item" id="1">
                    <img src={img} alt=""/>
                    <div className="text">
                        <h3>{title}</h3>
                        <p>{description}</p>
                    </div>
                    <div className="button"  onClick={this.handleClick} >Learn More</div>
                </div>
            </div>
        )
    }
}
